/* This file is dedicated to the Div classes for the home layout
   It is included in app.scss and controls the following divs:
    -Left:
      -topMenu, profile, bottomMenu
    -Center:
      -menu, Valve, Bottom
    -Right:
      -topMenu, profile, bottomMenu
*/
.topLeft-menu {
  width: 24%;
  height: auto;
  margin: 0 auto;
  left: 0;
  top: 5%;
  background-color: #D8D8D8;
  border: 1px solid;
  border-radius: 20px;
  opacity: 0.9; }

#welcome {
  width: 35%;
  height: auto;
  margin: 0 auto;
  background-color: white;
  opacity: 0.90;
  filter: alpha(opacity=87);
  /* For IE8 and earlier */
  border: 2px solid;
  border-radius: 25px;
  font-size: 100%;
  font-family: Georgia, serif;
  text-align: center;
  margin-top: 9.3%; }

#login {
  width: 62%;
  height: auto;
  margin: 0 auto;
  background-color: black;
  display: inline-block;
  opacity: 0.87;
  filter: alpha(opacity=87);
  /* For IE8 and earlier */
  border: 2px solid;
  border-radius: 25px;
  font-family: Georgia, serif; }

h3 {
  color: white;
  text-decoration: underline; }

hr {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  margin-left: auto;
  margin-right: auto;
  border-style: inset;
  border-width: 7px;
  border-color: green; }

label.login {
  color: white; }

html {
  background-image: url("/img/background3.jpg");
  background-repeat: no-repeat;
  min-width: 1375px;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover; }

#container {
  text-align: center;
  position: relative; }

